let getStorageType (src :string) =
    if src.StartsWith "data:" then OnChain
    else
        if src.StartsWith "ipfs://" then IPFS
        else Unknown

let getNftDisplay nft =
    let getExt (url :string) =
        let i = url.LastIndexOf('.')
        url.Substring(i + 1)
    let ipfsHttp (url :string) =
        if url.StartsWith "ipfs://ipfs" then
            "https://ipfs.io/ipfs/" + (url.Substring(11).TrimStart('/'))
        else
            if url.StartsWith "ipfs://" then
                "https://ipfs.io/ipfs/" + (url.Substring(7).TrimStart('/'))
            else
                url
    if nft.metadata <> null then
        let metadata = JS.JSON.parse nft.metadata
        let display =
            if metadata?animation_url <> null then
                match getExt metadata?animation_url with
                | "mp4" ->
                    { nft with display = Video <| (ipfsHttp metadata?animation_url, Mp4, getStorageType metadata?animation_url) }
                | "svg" ->
                    { nft with display = Image <| (ipfsHttp metadata?animation_url, Svg, getStorageType metadata?animation_url) }
                | _ ->
                    { nft with display = NoDisplay }
            else
                if metadata?image <> null then
                    let image :string = metadata?image
                    let format =
                        match getExt image with
                        | "svg" -> Svg
                        | "png" -> Png
                        | "gif" -> Gif
                        | "jpg" -> Jpg
                        | "webp" -> WebP
                        | _ -> UnknownFormat
                    { nft with display = Image <| (ipfsHttp image, format, getStorageType image) }
                else
                    { nft with display = NoDisplay }
        { display with title = metadata?name; description = metadata?description }
    else
        { nft with display = NoDisplay }

