module Alchemy =

    open Fable.SimpleHttp
    open Fable.SimpleJson

    type NFTContract = { address: string }

    type NFTContractMetadata = { name: string; symbol :string  }

    type NFTContractData = { address: string; contractMetadata: NFTContractMetadata }

    type NFTId = { tokenId: string }

    type NFTUri = { raw: string; gateway: string }

    type NFTResult =
        { contract: NFTContract
          id: NFTId
          title: string
          description: string option
          tokenUri: NFTUri
          media: NFTUri[]
          metadata: obj }

    type NFTResults =
        { ownedNfts: NFTResult[]
          totalCount: int }

    let getNFTs account =
        let url = Config.alchemyHttps + "/getNFTs/?owner=" + account + "&filters=SPAM"
        async {
            let! code, response = Http.get url
            match code with
            | 200 ->
                let results :NFTResults =
                    try
                        Json.parseAs response
                    with
                    | e ->
                        { ownedNfts = [||]; totalCount = 0 }
                let! contractData =
                    results.ownedNfts |> Array.map (fun n -> n.contract.address) |> Array.distinct
                        |> Array.map (fun contract ->
                            async {
                                let url = Config.alchemyHttps + "/getContractMetadata/?contractAddress=" + contract
                                let! code, response = Http.get url
                                match code with
                                | 200 ->
                                    let result :NFTContractData =
                                        try
                                            Json.parseAs response
                                        with
                                        | _ ->
                                            { address = contract; contractMetadata = { name = ""; symbol = "" } }
                                    return contract, result
                                | _ -> return failwith "error parsing contract data" }) |> Async.Parallel
                let contractDataByAddr = contractData |> Map.ofArray
                let getExt (url :string) =
                    let i = url.LastIndexOf('.')
                    url.Substring(i + 1)
                let ipfsHttp (url :string) =
                    if url.StartsWith "ipfs://ipfs" then
                        "https://ipfs.io/ipfs/" + (url.Substring(11).TrimStart('/'))
                    else
                        if url.StartsWith "ipfs://" then
                            "https://ipfs.io/ipfs/" + (url.Substring(7).TrimStart('/'))
                        else
                            url
                return
                    results.ownedNfts |> Array.map (fun nft ->
                        let mediaUri =
                            if nft.media.Length > 0 then
                                if System.String.IsNullOrEmpty(nft.media.[0].gateway) then nft.media.[0].raw else nft.media.[0].gateway
                            else
                                nft.metadata?image
                        
                        let display =
                            if nft.metadata <> null then
                                if not <| System.String.IsNullOrEmpty(nft.metadata?animation_url) then
                                    // animation
                                    match getExt nft.metadata?animation_url with
                                    | "mp4" -> Video (ipfsHttp nft.metadata?animation_url, Mp4, getStorageType nft.metadata?animation_url)
                                    | "svg" -> Image (ipfsHttp nft.metadata?animation_url, Svg, getStorageType nft.metadata?animation_url)
                                    | _ -> NoDisplay
                                else
                                    // image
                                    let format =
                                        match getExt nft.metadata?image with
                                        | "svg" -> Svg
                                        | "png" -> Png
                                        | "gif" -> Gif
                                        | "jpg" -> Jpg
                                        | "webp" -> WebP
                                        | _ -> UnknownFormat
                                    let storageType = getStorageType nft.metadata?image
                                    Image (mediaUri, format, storageType)
                            else NoDisplay
                        {   token_id = nft.id.tokenId
                            token_uri = nft.tokenUri.raw
                            token_address = nft.contract.address
                            block_number = ""
                            block_number_minted = ""
                            name =
                                match contractDataByAddr |> Map.tryFind nft.contract.address with
                                | None -> nft.title
                                | Some name -> name.contractMetadata.name
                            symbol =
                                match contractDataByAddr |> Map.tryFind nft.contract.address with
                                | None -> nft.title
                                | Some name -> name.contractMetadata.symbol
                            metadata = ""
                            display = display
                            title = nft.title
                            description =
                                match nft.description with | None -> "" | Some d -> d })
            | _ -> return failwith "error getting nfts"
        }


module Moralis =

    open Fable.SimpleHttp
    open Fable.SimpleJson

    type PagedResult<'t> =
        { total: int
          page: int
          page_size: int
          cursor: string
          result: 't[] }

    let getNFTs account =
        let url = sprintf "https://deep-index.moralis.io/api/v2/%s/nft?chain=eth&format=decimal" account
        let rec getNFTs cursor =
            let url =
                match cursor with
                | Some cursor ->
                    url + "&cursor=" + cursor
                | None -> url
            async {
                let! response =
                    Http.request url
                        |> Http.method GET
                        |> Http.header (Headers.accept "application/json")
                        |> Http.header (Headers.create "X-API-Key" Config.network.moralisApiKey)
                        |> Http.send
                let nfts :PagedResult<obj> = Json.parseAs response.responseText
                if nfts.total = nfts.result.Length then
                        
                    return nfts.result
                else
                    let! rest = getNFTs (Some nfts.cursor)
                    return Array.append nfts.result rest
            }
        async {
            let! nfts = getNFTs None
            let toFetch = nfts |> Array.filter (fun n -> n?metadata = null)
            let! _ =
                toFetch
                    |> Array.map (fun n ->
                        async {
                            let! code, response = Http.get n?token_uri
                            match code with
                            | 200 -> n?metadata <- response
                            | _ -> () })
                    |> Async.Parallel
            return nfts
                |> Array.map (fun n ->
                    let nft = n :?> NFT
                    // get display
                    getNftDisplay nft) }

