module New.View

open Fable.Core
open Fable.Core.JsInterop
open Fable.React
open Fable.React.Props
open Types
open Common
open System.Globalization

module Validate =

    let linkName txt = if System.String.IsNullOrEmpty txt then false else true

    let linkUrl txt = System.Text.RegularExpressions.Regex.IsMatch(txt, "^(ht|f)tp(s?)\:\/\/[0-9a-zA-Z]([-.\w]*[0-9a-zA-Z])*(:(0-9)*)*(\/?)([a-zA-Z0-9\-\.\?\,\'\/\\\+&%\$#_]*)?$")

let nfts (model :Model) tree dispatch =
    let getFormatName =
        function
        | Svg -> "SVG"
        | Png -> "PNG"
        | Gif -> "GIF"
        | Jpg -> "JPEG"
        | WebP -> "WebP"
        | UnknownFormat -> "Image"
    let getStorageName =
        function
        | OnChain -> "On-chain"
        | IPFS -> "IPFS"
        | Unknown -> "Server"
    let getVideoFormatName =
        function
        | Mp4 -> "MP4"
        | _ -> "Video"
    let getMediaText =
        function
        | Image (_, _, OnChain) ->
            "On-chain Image"
        | Image (_, format, storage) -> (getFormatName format) + " on " + (getStorageName storage)
        | Video (_, format, storage) -> (getVideoFormatName format) + " on " + (getStorageName storage)
        | _ -> ""
    let getMediaElement display =
        let txt = getMediaText display
        match display with
        | Image (_, _, OnChain) ->
            [span [ClassName "icon on-chain is-small"] [i [ClassName "mdi mdi-vector-square"] []]; span [] [str txt]]
        | Image (_, _, IPFS) | Video (_, _, IPFS) ->
            [span [ClassName "icon ipfs is-small"] [i [ClassName "mdi mdi-earth"] []]; span [] [str txt]]
        | _ -> [span [] [str txt]]
    let shouldDisplay (nft :NFT) =
        match model.treeEdit with
        | Some _ -> true
        | _ -> not (tree.nfts |> Map.containsKey (nft.token_address, nft.token_id))
    let classes (nft: NFT) =
        match model.treeEdit with
        | Some tree when tree.nftFields |> Map.containsKey (nft.token_address, nft.token_id) ->
            ["hidden", true]
        | _ -> []
    match model.nfts with
    | None -> div [ClassName "lds-dual-ring"] []
    | Some nfts ->
        div [ClassName "gallery"]
            (nfts |> Array.map (fun collection ->
                div [ClassName "collection"]
                    [h1 [] [str collection.name]
                     ofOption <| if System.String.IsNullOrEmpty(collection.description) then None else Some <| h2 [] [str collection.description]
                     div [ClassName "nfts"]
                        (collection.nfts |> Array.map (fun nft ->
                        match shouldDisplay nft with
                        | false -> ofOption None
                        | true ->
                            match nft.display with
                            | NoDisplay -> ofOption None
                            | Image _ | Video _ ->
                                div []
                                    [// MEDIA
                                     match nft.display with
                                     | Image (src, _, _) -> img [classList <| classes nft; Src src]
                                     | Video (src, ty, _) ->
                                        let mime =
                                            match ty with
                                            | Mp4 -> "video/mp4"
                                        video [classList <| classes nft; AutoPlay false; Controls true] [source [Src src; Type mime]]
                                     // EDIT CONTROLS
                                     match model.treeEdit with
                                     | None -> ofOption None
                                     | Some tree ->
                                         div [ClassName "edit-nft"]
                                            [button [ClassName "button"; OnClick (fun _ -> dispatch <| ToggleNft (nft.token_address, nft.token_id) )] [
                                                match tree.nftFields |> Map.tryFind (nft.token_address, nft.token_id) with
                                                | None ->
                                                    span [ClassName "icon"] [i [ClassName "mdi mdi-eye-off"] []]; span [] [str "Hide"]
                                                | Some _ ->
                                                    span [ClassName "icon"] [i [ClassName "mdi mdi-eye"] []; span [] [str "Show"]]]]
                                     // CARD
                                     div [ClassName "nft-card"] [
                                        p [ClassName "nft-title"] [str nft.title]
                                        p [ClassName "nft-media"] (getMediaElement nft.display)
                                        ofOption <|
                                            if System.String.IsNullOrEmpty(collection.description) && not <| System.String.IsNullOrEmpty(nft.description) then
                                                Some <| p [ClassName "nft-desc"] [str nft.description]
                                            else None]]
                            ))]))

let footerElement =
    footer [ClassName "footer"]
        [a [Href "/"]
            [img [Src "/img/tree.svg"] 
             h1 [] [str "Web3 Tree"]]]

let editBar canSave dispatch =
    nav [ClassName "navbar edit is-fixed-top"]
        [div [ClassName "navbar-brand"]
            [div [ClassName "navbar-item"] [span [ClassName "icon"] [i [ClassName "mdi mdi-cog-outline"] []]; span [] [str "Editing"]]]
         div [ClassName "navbar-item controls"]
            [button [ClassName "button is-info"; Disabled <| not canSave] [span [ClassName "icon"] [i [ClassName "mdi mdi-content-save-outline"] []]; span [] [str "Save"]]
             ]]

let editLink dispatch displayEditBtns (olink :TreeLink) n index (link :TreeEditLink) =
    let valid = Validate.linkUrl link.urlField && Validate.linkName link.nameField
    let revertDeletes = link.name = "" && link.url = ""
    div [classList ["editing", link.editing; "field", true; "edit-link", true]]
        [div [ClassName "name"] [str link.name]
         div [ClassName "control"]
            (match link.editing with
             | true ->
                let nameValid = link.name <> link.nameField && Validate.linkName link.nameField
                let nameInvalid = link.name <> link.nameField && not <| Validate.linkName link.nameField
                let urlValid = link.url <> link.urlField && Validate.linkUrl link.urlField
                let urlInvalid = link.url <> link.urlField && not <| Validate.linkUrl link.urlField
                [input [Id link.key; classList ["input", true ;"link-name", true; "valid", nameValid; "invalid", nameInvalid]; Type "text"; Placeholder "Title"; DefaultValue link.nameField; OnChange (fun e -> UserInput(LinkName link.key, e.target?value) |> dispatch )]
                 input [classList ["input link-name", true; "valid", urlValid; "invalid", urlInvalid]; Type "text"; Placeholder "Url"; DefaultValue link.urlField; OnChange (fun e -> UserInput(LinkUrl link.key, e.target?value) |> dispatch )] ]
             | false -> [])
         div [ClassName "control-buttons"]
            [if link.editing then button [ClassName "control-cancel"; OnClick (fun _ -> dispatch <| (if revertDeletes then LinkDelete else LinkCancel) link.key)] [span [ClassName "icon"] [i [ClassName "mdi mdi-arrow-u-left-top-bold mdi-24px"] []]] else ofOption None
             if link.editing then button [Disabled <| not valid; ClassName "control-accept"; OnClick (fun _ -> dispatch <| LinkAccept link.key)] [span [ClassName "icon"] [i [ClassName "mdi mdi-check-outline mdi-24px"] []]] else ofOption None
             if displayEditBtns then button [ClassName "control-edit"; OnClick (fun _ -> dispatch <| LinkEdit link.key)] [span [ClassName "icon"] [i [ClassName "mdi mdi-square-edit-outline mdi-24px"] []]] else ofOption None
             if index = 0 || link.editing then ofOption None else button [ClassName "control-up"; OnClick (fun _ -> dispatch <| LinkUp link.key)] [span [ClassName "icon"] [i [ClassName "mdi mdi-arrow-up-box mdi-24px"] []]]
             if index = n - 1 || link.editing then ofOption None else button [ClassName "control-down"; OnClick (fun _ -> dispatch <| LinkDown link.key)] [span [ClassName "icon"] [i [ClassName "mdi mdi-arrow-down-box mdi-24px"] []]]
             if link.editing then ofOption None else button [ClassName "control-delete"; OnClick (fun _ -> dispatch <| LinkDelete link.key)] [span [ClassName "icon"] [i [ClassName "mdi mdi-delete mdi-24px"] []]] ]]

let editName tree dispatch =
    div [ClassName "field edit-name"]
        [div [ClassName "control"]
            [input [ClassName "input"; Type "text"; Placeholder "Name"; DefaultValue tree.nameField; OnChange (fun e -> UserInput(Name, e.target?value) |> dispatch)]]]

let link (link :TreeLink) =
    a [ClassName "button"; Href link.url] [str link.name]

let tree model common dispatch =
    match model.tree with
    | None -> [div [ClassName "lds-dual-ring"] []]
    | Some tree ->
        [   ofOption <|
               match model.treeEdit with
               | Some treeEdit -> Some <| editBar false dispatch
               | None -> None
            match model.treeEdit with
            | Some tree -> editName tree dispatch
            | None ->
               h1 [] [
                  match tree.name with
                  | "" -> str model.address.Value
                  | name -> str name]
            
            match model.treeEdit, tree.links with
            | None, [||] -> ofOption None
            | None, _ ->
                ofList <|
                    [div [ClassName "section-header"]
                        [h1 [] [i [ClassName "mdi mdi-leaf"] []; str "Links"]]
                     div [ClassName "links"] (tree.links |> Array.map link |> Array.toList)]
            | Some etree, _ ->
                let displayEditBns = etree.linkFields |> Array.exists (fun l -> l.editing) |> not
                ofList <|
                    [div [ClassName "section-header"]
                        [h1 [] [i [ClassName "mdi mdi-leaf"] []; str "Links"]]
                     div [ClassName "links edit-links"] (etree.linkFields |> Array.mapi (fun i elink -> editLink dispatch displayEditBns tree.links.[elink.index] etree.linkFields.Length i elink) |> Array.toList)
                     button [Disabled <| not displayEditBns;ClassName "add-link button"; OnClick (fun _ -> dispatch LinkAdd)]
                        [span [ClassName "icon"] [i [ClassName "mdi mdi-link-plus"] []]; span [] [str "Add"]]]
            div [ClassName "section-header"] [h1 [] [img [Src "/img/hex.svg"]; span [] [str "NFT Gallery"]]]
            nfts model tree dispatch]

let root model common dispatch =
    div [ClassName "has-text-centered tree"]
        [ofList <| tree model common dispatch
         footerElement]
