module New.State

open Types
open Common
open Elmish
open Fable.Core.JsInterop

let editTree tree =
    { nameField = tree.name
      bioField = tree.bio
      linkFields =
        tree.links |> Array.mapi (fun i link ->
            { name = link.name
              url = link.url
              nameField = link.name
              urlField = link.url
              editing = false
              index = i
              key = System.Guid.NewGuid().ToString() })
      nftFields = tree.nfts }

let update msg model : Model * Cmd<Msg> =
    match msg with
    | SetAddress address ->
        { model with address = Some address },
            Cmd.batch
                [Cmd.OfAsync.perform Alchemy.getNFTs address SetNfts
                 Cmd.OfAsync.perform Server.getTreeData address SetTreeData]
    | SetNfts nfts ->
        let collections =
            nfts
                |> Array.groupBy (fun n -> n.token_address)
                |> Array.map (fun (_, nfts) ->
                    let description =
                        if nfts |> Array.forall (fun n -> n.description = nfts.[0].description) then nfts.[0].description else null
                    { Common.NFTCollection.symbol = nfts.[0].symbol
                      Common.NFTCollection.name = nfts.[0].name
                      Common.NFTCollection.nfts = nfts
                      Common.NFTCollection.description = description })
        Browser.Dom.window?collections <- collections
        { model with nfts = Some collections }, Cmd.none
    | SetTreeData tree ->
        match model.mode with
        | View ->
            { model with tree = Some tree }, Cmd.none
        | Edit ->
            { model with tree = Some tree; treeEdit = Some <| editTree tree }, Cmd.none
    | UserInput (field, value) ->
        match model.treeEdit with
        | Some tree ->
            match field with
            | Name ->
                { model with treeEdit = Some { tree with nameField = value } }, Cmd.none
            | LinkName id ->
                let links = tree.linkFields |> Array.map (fun f ->
                    if f.key = id then { f with nameField = value } else f)
                { model with treeEdit = Some { tree with linkFields = links } }, Cmd.none
            | LinkUrl id ->
                let links = tree.linkFields |> Array.map (fun f ->
                    if f.key = id then { f with urlField = value } else f)
                { model with treeEdit = Some { tree with linkFields = links } }, Cmd.none
    | ToggleNft (address, id) ->
        match model.treeEdit with
        | Some tree ->
            match tree.nftFields |> Map.tryFind (address, id) with
            | None ->
                { model with treeEdit = Some { tree with nftFields = tree.nftFields |> Map.add (address, id) true } }, Cmd.none
            | Some _ ->
                { model with treeEdit = Some { tree with nftFields = tree.nftFields |> Map.remove (address, id) } }, Cmd.none
    | LinkUp key ->
        match model.treeEdit with
        | Some tree ->
            let index = tree.linkFields |> Array.findIndex (fun l -> l.key = key)
            let a = tree.linkFields.[index - 1]
            let b = tree.linkFields.[index]
            let links = tree.linkFields |> Array.mapi (fun i l ->
                match i with
                | _ when i = index - 1 -> b
                | _ when i = index -> a
                | _ -> l)
            { model with treeEdit = Some { tree with linkFields = links } }, Cmd.none
    | LinkDown key ->
        match model.treeEdit with
        | Some tree ->
            let index = tree.linkFields |> Array.findIndex (fun l -> l.key = key)
            let a = tree.linkFields.[index + 1]
            let b = tree.linkFields.[index]
            let links = tree.linkFields |> Array.mapi (fun i l ->
                match i with
                | _ when i = index + 1 -> b
                | _ when i = index -> a
                | _ -> l)
            { model with treeEdit = Some { tree with linkFields = links } }, Cmd.none
    | LinkEdit key ->
        match model.treeEdit with
        | Some tree ->
            let links = tree.linkFields |> Array.map (fun l -> if l.key = key then { l with editing = true } else l)
            { model with treeEdit = Some { tree with linkFields = links } }, Cmd.none
    | LinkCancel key ->
        match model.treeEdit with
        | Some tree ->
            let links = tree.linkFields |> Array.map (fun l ->
                if l.key = key then { l with nameField = l.name; urlField = l.url; editing = false } else l)
            { model with treeEdit = Some { tree with linkFields = links } }, Cmd.none
    | LinkAccept key ->
        match model.treeEdit with
        | Some tree ->
            let links = tree.linkFields |> Array.map (fun l -> if l.key = key then { l with name = l.nameField; url = l.urlField; editing = false } else l)
            { model with treeEdit = Some { tree with linkFields = links } }, Cmd.none
    | LinkAdd ->
        match model.treeEdit with
        | Some tree ->
            let links = Array.append tree.linkFields [| { key = System.Guid.NewGuid().ToString(); name = ""; url = ""; nameField = ""; urlField = ""; editing = true; index = -1 } |]
            { model with treeEdit = Some { tree with linkFields = links } }, Cmd.none
    | LinkDelete key ->
        match model.treeEdit with
        | Some tree ->
            let links = tree.linkFields |> Array.choose (fun l -> if l.key = key then None else Some l)
            { model with treeEdit = Some { tree with linkFields = links } }, Cmd.none
    | _ -> model, Cmd.none
