module App.State

open Elmish
open Elmish.Navigation
open Elmish.UrlParser
open Fable.Core
open Fable.Core.JsInterop
open Browser
open Global
open Types
open Imports
open Common
open System

let pageParser: Parser<Page->Page,Page> =
    oneOf [
        map Home (s "")
        map Edit (s "edit" </> str)
        map View (str)
    ]

let urlUpdate gbl (result : Page option) model =
    let setClasses className =
        (document.getElementById "html").className <- className
        (document.getElementById "body").className <- className
    match result with
    | None ->
        console.error("Error parsing url")
        model, Navigation.modifyUrl (toUrl model.page)
    | Some Home ->
        // set class
        setClasses "page-home"
        { model with
            page = Home
                // need to load user data
             }, Cmd.none
    | Some ((View user) as page) | Some ((Edit user) as page) ->
        setClasses <|
            match page with
            | View _ -> "page-view"
            | Edit _ -> "page-edit"
            | _ -> ""
        { model with page = page; tree = { model.tree with mode = match page with | View _ -> New.Types.View | Edit _ -> New.Types.Edit } },
            Cmd.OfAsync.perform Server.getAddress user (NewMsg << New.Types.Msg.SetAddress)

let init gbl result =
    let (home, homeCmd) = Home.State.init()
    let (model, cmd) =
        urlUpdate gbl result
          { page = Home
            home = home
            tree =
                { nfts = None
                  tree = None
                  treeEdit = None
                  address = None
                  mode = New.Types.PageMode.View }
            common =
                { accountData = None } }
    model, Cmd.batch [ cmd
                       Cmd.map HomeMsg homeCmd ]

let update gbl msg model =
    let updateAccountCmd web3 =
        Cmd.OfPromise.perform
            (fun _ -> promise {
                let! chainId = web3?eth?getChainId()
                let! accounts = web3?eth?getAccounts()

                return { selectedAccount = accounts?at $ 0; chainId = chainId }
            }) () SetAccountData
    match msg with
    | HomeMsg Home.Types.Msg.Connect ->
        model, Cmd.OfPromise.perform gbl.web3Modal.connect () SetProvider
    | HomeMsg (Home.Types.Msg.ChangePage page) -> model, Navigation.newUrl <| toUrl page
    | HomeMsg msg ->
        let (home, homeCmd) = Home.State.update msg model.home
        { model with home = home }, Cmd.map HomeMsg homeCmd
    | NewMsg msg ->
        let (new_, newCmd) = New.State.update msg model.tree
        { model with tree = new_ }, Cmd.map NewMsg newCmd
    | SetProvider provider ->
        let web3 = new Web3(provider)
        gbl.web3 <- web3
        model, updateAccountCmd web3
    | SetAccountData accountData ->
        { model with common = { model.common with accountData = Some accountData } }, Cmd.none
    | ChangePage page ->
        model, Navigation.newUrl <| toUrl page
